<h2>Instance Properties</h2>
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>minHeight</td>
<td>Number</td>
<td>0</td>
<td><code v-pre>optional</code> Minimum elevation.</td>
</tr>
<tr>
<td>maxHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre>require</code> Maximum elevation.</td>
</tr>
<tr>
<td>speed</td>
<td>Number</td>
<td>10</td>
<td><code v-pre>optional</code> Submerged speed.</td>
</tr>
<tr>
<td>polygonHierarchy</td>
<td>Array</td>
<td></td>
<td><code v-pre>require</code> Specifies the latitude and longitude array that builds the submerged analysis polygon.</td>
</tr>
</tbody>
</table>
<hr>
<h2>Events</h2>
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>activeEvt</td>
<td>{isActive: Boolean}</td>
<td>Triggered when the <code v-pre>flooding</code> state changes in the flood analysis component, and returns whether the flood analysis starts.</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
      data() {
        return {
          minHeight: 0,
          maxHeight: 4000,
          speed: 10,
          polygonHierarchy: [
            { lng: 102.1, lat: 29.5 },
            { lng: 106.2, lat: 29.5 },
            { lng: 106.2, lat: 33.5 },
            { lng: 102.1, lat: 33.5 }
          ],
          url: 'https://dev.virtualearth.net',
          bmKey: 'AgcbDCAOb9zMfquaT4Z-MdHX4AsHUNvs7xgdHefEA5myMHxZk87NTNgdLbG90IE-', // 可到(https://www.bingmapsportal.com/)申请Key。
          flooding: false
        }
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const { Cesium, viewer } = this.cesiumInstance
          viewer.scene.globe.depthTestAgainstTerrain = true
          viewer.camera.setView({
            destination: new Cesium.Cartesian3(-1432246.8223880068, 5761224.588247942, 3297281.1889481535),
            orientation: {
              heading: 6.20312220367255,
              pitch: -0.9937536846355606,
              roll: 0.002443376981836387
            }
          })
        },
        toggle() {
          this.$refs.flood.flooding = !this.$refs.flood.flooding
        },
        activeEvt(_) {
          this.flooding = _.isActive
        },
        clear() {
          this.$refs.flood.unload()
        }
      }
    }
  </script>